<template>
<v-list-group style="width: 100%; overflow-x: hidden" :value="value[0] === 'info_item_title'">
  <template v-slot:activator>
    <v-list-item-icon v-if="titleIcons[value[0]]"><v-icon>{{titleIcons[value[0]]}}</v-icon></v-list-item-icon>
    <v-list-item-title>{{titleNames[value[0]] || value[0]}}</v-list-item-title>
  </template>
  <v-list dense style="width: 100%; overflow-x: hidden !important" >
    <v-list-item v-for="data in Object.entries(value[1])" :key="data[0]" :data="data" two-line>
      <v-list-item-content>
        <v-list-item-title>{{dataNames[data[0]] || data[0]}}</v-list-item-title>
        <v-list-item-subtitle>
            <IpParser v-if="data[0] === 'info_item_ip'" :value="data[1]"/>
            <span v-else>{{data[1]}}</span>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
  </v-list>
</v-list-group>
</template>

<script>
import IpParser from '@/render/components/Ticket/Parsers/IpParser'

export default {
  components: { IpParser },
  props: ['value'],

  data: () => ({
    titleNames: {
      info_customer_title: '',
      info_ticket_title: '',
      info_item_title: ''
    },
    titleIcons: {
      info_customer_title: 'mdi-account',
      info_ticket_title: 'mdi-alpha-t-circle',
      info_item_title: 'mdi-cloud'
    },
    dataNames: {
      // 
      info_customer_regdate: ' ',
      info_customer_country: '',
      info_customer_theme: '',
      // 
      info_ticket_id: 'ID',
      info_customer_ticket: ' ',
      // 
      info_item_opendate: ' ',
      info_item_ostempl: '',
      info_item_password: '',
      info_item_ip: 'IP',
      info_item_serverid: 'ID',
      info_item_processingmodule: '',
      info_item_processingmodule_itemcount: '-',
      info_item_username: ''
    }
  })
}
</script>
